import React, { Component } from 'react';
import { NavLink } from 'react-router-dom';
import { Flex } from 'antd-mobile';

import Intro from '../../component/Intro';

import './index.less';
import entry_01 from '../../assets/images/entry_01.png';
import jxh from '../../assets/images/jxh18.png'
import bgMap from '../../assets/images/bg-map.png'
import { passMenus } from './passMenus';

export default class Pass extends Component {
  state = {
    showMore: false
  }

  showMore() {
    this.setState({
      showMore: !this.state.showMore
    })
  }
  renderModuleMenu() {
    return (
      <React.Fragment>
        {passMenus.map(menu => (
          <NavLink to={`/tong/platform/list?title=${menu.title}`} key={menu.id} className={`pass-menu-item`} >
            <div>
              <p>{menu.title}</p>
              <p>{menu.desc}</p>
            </div>
          </NavLink>
        ))}
      </React.Fragment>
    )
  }
  renderNavModule() {
    return (
      <div className="pass-content-container"
        style={{
          backgroundImage: `url(${bgMap})`
        }}
      >
        <div className="pass-nav-module">
          {this.renderModuleMenu()}
          <div className="module-content">
            <img alt="" src={jxh}
              // width="100"
              // height="100"
              style={{
                width: '80%'
              }}
            />
          </div>
        </div>
      </div>
    )
  }


  render() {
    return (
      <Flex
        direction="column"
        align="stretch"
      >
        <div className="page-content">
          <Intro bgUrl={entry_01} title="" desc="" />
          <div onClick={() => this.showMore()} className={this.state.showMore ? 'about' : 'about textover'}>
            <h4>
              
              <span>{this.state.showMore ? '<' : '>'}</span>
            </h4>
            <div>""</div>
          </div>
          {this.renderNavModule()}
        </div>
      </Flex>
    )
  }
};
